.App-header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.content {
  text-align: center;
}

.phone-input {
  margin-top: 20px;
  padding: 10px;
  font-size: 16px;
}

.join-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: purple;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.join-button:hover {
  background-color: darkviolet;
}
